"use client";

import { useGetProductsQuery } from "@/state/api";
import { SearchIcon } from "lucide-react";
import { useState } from "react";
import Header from "@/app/{components}/Header";

const Products = () => {
const [searchTerm, setSearchTerm] = useState("");
const [isModelOpen, setIsModelOpen] = useState(false);

const {
    data: products, isLoading, isError
} = useGetProductsQuery(searchTerm);

if( isLoading ) {
     return <div
     className=" py-4">Loading...</div>
}

 if( isError  || !products) {
     return  (<div
     className=" text-center text-500 py-4"> Error Failed To Fecth </div>
 );
}


  return (
    <div
        className="mx-value pb-5 w-full "
    >
    {/* Search Bar */}
    <div className="mb-6">
        <div className="flex items-center border-2 border-gray-200 rounded">
            <SearchIcon className="w-6 h-6 text-gray-500 m-2" />  
            <input    className="bg-white px-2 rounded py-2 w-full"
                type="text"
                placeholder="Search Products..."

                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
            />
        </div>
    </div> 

   {/* HEADER BAR*/}
   <div className="flex justify-between items-center mb-6">
    <Header name="Products" />
       <button
       onClick={() => setIsModelOpen(true)}
       className="flex iems-center font-bold hover:bg-blue-500 text-gray-300 px-4 py-2 rounded"
       >Add Product</button>

    </div>
    </div>
  );
};

export default Products;

